.overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: rgba(0, 0, 0, 0.8);
  z-index: 1200;
}

.content {
  max-width: calc(100vw - 48px);
  max-height: calc(100vh - 24px);
}

.close {
  font-size: 24px;
  font-weight: bold;
  font-family: Arial, 'Helvetica Neue', Helvetica, sans-serif;
  cursor: pointer;
  color: #303f9f;
  position: absolute;
  top: 10px;
  right: 30px;
}
